import React from 'react';
import {Engagement, Innovation, International, Personal, Seminar, Time} from "../../data/activity_data";

export const Article = "Leesbaar artikel";

const Pin = ({type}) => {
    if (type === Innovation)
        return <div className="timeline-pin pin-innovation"><i className="fas fa-flask"/></div>;
    else if (type === International)
        return <div className="timeline-pin pin-international"><i className="fa fa-plane"/></div>;
    else if (type === Seminar)
        return <div className="timeline-pin pin-seminar"><i className="fa fa-microphone"/></div>;
    else if (type === Personal)
        return <div className="timeline-pin pin-personal"><i className="fa fa-user"/></div>;
    else if (type === Engagement)
        return <div className="timeline-pin pin-engagement"><i className="fa fa-walking"/></div>;
    else if (type === Time)
        return <div className="timeline-pin pin-time"><i className="fa fa-calendar"/></div>;
    else if (type === Article)
        return <div className="timeline-pin pin-article"><i className="fa fa-pen"/></div>;
};

export default Pin;
