import React from 'react';
import "./article.scss";

const Profile = () => {
    return (
        <>
            <div className="profile-img ">
                <img src="/images/profile.PNG" alt="profile" width={150}/>
            </div>
            <hr/>
            <p>Mijn naam is Jens Beernaert. Ik ben geboren op 1 januari 1998 en ben dus momenteel 22 jaar. Mijn vrije
                tijd besteed ik zo veel mogelijk aan fitness of lopen. Daarnaast speel ik ook graag op de Playstation
                met mijn vrienden. Maar de meeste tijd van mijn dag gaat naar informatica.</p>
            <p>Mijn interesse voor informatica is ontstaan tijdens mijn opleiding in het middelbaar,
                Wetenschappen-Wiskunde. Ik ben altijd wel bezig geweest met technologie, daarom had ik twee uur
                “Informatica voor beginners” als keuzevak gekozen. Maar tot mijn grote teleurstelling was deze cursus
                volledig in Scratch, een programmeertaal voor kinderen. Ik vond dit zo’n ongelofelijke ontgoocheling,
                dat ik toen beslist heb om ‘echte’ informatica te gaan studeren na het middelbaar.</p>
            <p>Ik heb eerst twee jaar Informatica gestudeerd aan de Universiteit van Hasselt. Hoewel het eerste semester
                een hele openbaring was, werd ik daarna helemaal bedolven onder een enorme berg wiskunde. Ik voelde me
                helemaal thuis in de programmeerwereld, maar kon mij niet genoeg motiveren om te studeren voor de andere
                vakken. In de zomer van 2017 heb ik mij dus ingeschreven op de PXL voor de opleiding Toegepaste
                Informatica.</p>
            <p>Ik ben toen mijn huidige opleiding begonnen met een hoop basiskennis. Hierdoor had ik een vijftal
                vrijstellingen en dus extra tijd in mijn eerste en tweede jaar. Dit was voor mijn de perfecte
                mogelijkheid om te doen wat ik altijd al wou doen: meer bijleren over informatica. Ik heb dus tijdens
                mijn loopbaan op de PXL zelf enkele projecten kunnen uitwerken.</p>
            <p>Nu ik bijna afgestudeerd ben, hoop ik dat ik de komende vijf jaar kan blijven bijleren. Mijn grootste
                angst is dus om later een job zonder doorgroeimogelijkheden te hebben. Daarom dat het idee in mijn hoofd
                speelt om, na enkele jaren veldervaring, zelf een bedrijf te beginnen. Hoewel ik nog totaal geen idee
                heb hoe ik hieraan kan beginnen, is het de perfecte motivatie om te constant bij te leren.</p>
        </>
    )
};

export default Profile;
