import React from 'react';
import "./article.scss";

const Reflection = () => {
    return (
        <>
            <span className="display-4">Reflectie</span>
            <hr/>
            <p>Ongeveer drie jaar geleden, in september 2017, ben ik begonnen aan dit avontuur. Ik had al twee jaar
                universiteit achter de rug en ik kende ook al iemand die in deze opleiding zat, dus ik wist wel wat ik
                moest verwachten van deze richting. Toch heb ik meer geleerd dan ik initieel verwacht had. Drie jaar
                geleden dacht ik dat ik vooral ging leren programmeren. Vandaag weet ik hoe ik hele projecten moet
                maken.</p>
            <div className="illustration">
                <img src="/images/pxl.png" alt="pxl"/>
                <p className="text-muted">PXL Hogeschool</p>
            </div>
            <p>Toen ik begon aan de PXL, wou ik dus vooral leren programmeren. Ik was tijdens het middelbaar al bezig
                met het maken van kleine projectjes en ik ben ook gestopt met universiteit om mijn <b>passie</b> voor coderen
                te volgen. Als ik nu bekijk welke technische kennis ik beheer, kan ik met zekerheid zeggen dat ik deze
                opleiding met voldoende hard skills kan verlaten. Als ik mijn examenresultaten van de voorbije jaren
                bekijk, zie ik ook dat de pure programmeervakken er met kop en schouders bovenuit steken.</p>
            <p>Ook na de lesuren heb ik de tijd genomen om mezelf <b>ondernemend</b> en innovatief te ontwikkelen. Ik heb deze
                laatste drie jaar mijn vrije tijd gespendeerd aan het vergaren van kennis. In totaal heb ik zelf een
                drietal eigen projecten uitgewerkt. Daarnaast heb ik meerdere Pluralsight-cursussen gevolgd en
                verschillende seminaries bijgewoond.</p>
            <p>Ik heb niet alleen mijn hard skills kunnen uitbouwen, maar ook mijn soft skills zijn er de laatste jaren
                op vooruit gegaan. In het begin van mijn opleiding hield ik mij vooral op de achtergrond bij
                groepswerken. Ik vond het minder belangrijk om mijn eigen ideeën aan te brengen. Na talloze projecten en
                presentaties is dat helemaal anders. Nu vind ik het zelf aangenamer om zelf het voortouw te nemen
                tijdens een project. Het klinkt misschien wel cru, maar enkele ervaringen met minder goede teamleden
                hebben mij geholpen om mijzelf <b>multidisciplinair</b> te ontwikkelen. Zo wil ik bijvoorbeeld van iedereen
                weten aan welke feature hij of zij aan het werken is. Dit zorgt ervoor dat er geen misverstanden zijn en
                dat maakt het samenwerken natuurlijk een stuk gemakkelijker.</p>
            <p>En misschien het belangrijkste wat ik kan wegnemen van deze opleiding, ik heb plezier gehad. Tijdens de
                laatste jaren is mijn passie voor informatica alleen maar groter geworden. Voor mij was dit laatste jaar
                de kers op de taart. Het IT-project was voor mij één van de leukste projecten, die ik samen met de meest
                aangename teamleden, heb mogen uitwerken. Ook de Duitslandreis was een evenement dat ik mij nog lang zal
                herinneren. Niet alleen vond ik het leuk om ook op <b>internationaal</b> niveau een project uit te werken, maar
                ik heb ook een goede tijd beleefd met mijn vrienden.</p>
            <div className="illustration">
                <img src="/images/x-factor.jpg" alt="x-factor"/>
                <p className="text-muted">De PXL X-factor</p>
            </div>
            <p>Het viel misschien al wel op door de gemarkeerde woorden in de bovenstaande tekst, maar ik deze laatste
                drie jaar mijn best gedaan om mijn X-factor zo goed mogelijk te ontwikkelen. De X-factor is de blauwdruk
                voor de visie van de PXL, om elk jaar weer nieuwe excellente professionals te creëren. Ik ben de laatste
                drie jaar, zonder er zelf bij stil te staan, gegroeid in elk van deze vier pijlers. Hoewel mijn laatste
                dagen op de schoolbanken van de PXL geteld zijn, hoop ik dat ik de rest van mijn carrière mijzelf
                volgens de X-factor verder kan ontwikkelen.</p>
        </>
    )
};

export default Reflection;
