import React from 'react';

const Baelor = () => {
    return (
        <>
            <span className="display-4">Project Baelor</span>
            <hr/>
            <p>Ik weet van mijzelf dat ik leerstof liever op mijn eigen tempo verwerk. Dit mag dan wel betekenen dat ik
                niet in elk les aanwezig was, ik zorgde wel dat ik alle leerstof op mijzelf leerde. Ik ben ook altijd
                van mening geweest dat de school ervoor zorgde dat dit perfect mogelijk was. Ik vond het enorm handig
                dat de school ons een platform zoals Pluralsight heeft aangereikt. Dit zorgde ervoor dat ik de leerstof
                aan de hand van een videocursus zelf kon doornemen.</p>
            <p>Pluralsight zorgde niet alleen dat ik kon bijblijven met de les, maar ook dat ik op mezelf extra kennis
                kon opdoen. Voor mij stopt informatica niet na de lesuren. Ik wil voor mijzelf zoveel mogelijk hard
                skills bijleren. Daarom vond ik het spijtig dat er in het begin van het derde jaar een keuze moest
                gemaakt worden tussen Web of Enterprise. Ik heb toen gekozen voor Enterprise, maar ik heb die
                zomervakantie gebruikt om op mijzelf React te leren. Hiervoor heb ik dan ook handig gebruik gemaakt van
                Pluralsight. Het plan was dan ook om deze kennis direct te gebruiken en zelf een project uit te werken
                voor een fictionele klant, enkele vrienden.</p>
            <p>De eerst stap was om de juiste cursus te vinden. Ik ben uiteindelijk geland op “React: Getting Started”
                van Samer Buna. Deze cursus is één van de officiële cursussen die Pluralsight aanbiedt om React te
                leren. Deze cursus had ook de meeste ratings van elke React cursus en had zelfs een score van meer dan
                vier en een halve ster. Ten slotte had deze cursus ook een vervolgcursus van dezelfde auteur: “Advanced
                React.js”. Ik kon dus van start gaan met een degelijke cursus.</p>
            <p>De cursus zelf vond ik goed opgebouwd. Elk hoofdstuk bestond uit een miniprojectje dat afgewerkt werd
                doorheen de lessen. De verhouding tussen uitleg en oefening zat goed. Na elk hoofdstuk nam ik ook zelf
                wat tijd om met de nieuwe kennis wat te experimenteren. Het goede weer zorgde ervoor dat ik vooral ’s
                avonds met de cursus bezig was. Na ongeveer anderhalve week was ik dus klaar met de vier uur durende
                cursus.</p>
            <p>Daarna volgde, volgens mijn mening, het leukste deel. Ik heb enkele vrienden die het Playstation-spel
                FIFA speelden. Zij hadden een manier nodig om statistieken van hun spelers bij te houden. Op dat moment
                hielden zij gewoon een spreadsheet bij, maar zij vroegen of ik hier geen webapplicatie voor kon maken.
                Ik zag dit als de ideale kans om mijn nieuwe kennis te gebruiken. Ook zou dit een goede oefening zijn op
                de developer-klant relatie. Zij gaven mij elke paar weken een paar features om uit te werken en daarna
                kwamen we samen om te bespreken wat zij ervan vonden.</p>
            <p>Rond augustus ben ik dan effectief begonnen met het maken van de website. Aangezien ik mij ook wat verder
                wou verdiepen in ASP.NET, heb ik die technologie gebruikt voor de backend. Ik heb daarnaast ook gebruik
                gemaakt van de gelegenheid om wat meer CSS-styling bij te leren.</p>
            <div className="illustration">
                <img src="/images/activity/baelor.png" alt="baelor"/>
                <p className="text-muted">Spelerspagina Baelor</p>
            </div>
            <p>Midden augustus was ik klaar met de eerste versie van de website, die ik “Baelor” genoemd had. Ik heb
                deze dan ook overlopen met mijn ‘klanten’ en zij waren zeer tevreden. Dit project ben ik nog blijven
                uitbreiden tot november 2019. Toen ben ik gestopt door tijdsgebrek door de examens van dat semester.</p>
            <div className="illustration">
                <img src="/images/activity/baelor2.png" alt="baelor"/>
                <p className="text-muted">Leaguepagina Baelor</p>
            </div>
            <p>Hoewel dit geen officiële I-Talent activiteit is, vind ik dit misschien wel de activiteit waar ik het
                meeste aan gehad heb in mijn loopbaan aan de PXL. Je krijgt van de PXL veel leerstof om te verwerken
                tijdens je opleiding. Deze leerstof wordt in een vertrouwde omgeving aan jou aangeboden zodat je zonder
                moeilijkheden kan bijleren. Je krijgt oefeningen, je moet examens doen en noem maar op.</p>
            <p>Maar iedereen weet ook dat informatica een wereld is die blijft veranderen. Wij gaan dus continu nieuwe
                frameworks en technologieën moeten bijleren. Deze zullen wij zelf moeten bijleren. Wij gaan niet elke
                keer terug naar school gaan als er een nieuwe programmeertaal uitkomt. Daarom vond ik het belangrijk dat
                ik mijzelf ben blijven aanzetten tot het opnemen van nieuwe kennis, zoals ik dat later ook zal moeten
                doen.</p>
            <p>Ik heb ook bijgeleerd over applicaties maken die voor een klant bedoeld zijn. Als je voor jezelf een
                projectje uitwerkt, weet je zelf perfect wat je wil. Als je voor iemand anders werkt, moet je eerst
                zorgen dat je op dezelfde golflengte zit. Het is een paar keer voorgevallen dat ik een feature gemaakt
                had en dat dat eigenlijk niet helemaal overeenstemde met wat mijn vrienden wilden. Hieruit heb ik dus
                meegenomen dat je een goede lijst met afspraken moet hebben met je klant. Als je op papier kan zetten
                wat de klant verwacht, is het ook makkelijker om direct aan de slag te gaan. Het was ook handig om
                eerste enkele schetsen of mockups te maken van de applicatie.</p>
            <p>Dit hele gebeuren, van de cursus tot het project zelf, is voor mij een bepalend moment geweest als
                developer. Dit project heeft mij dan ook aangezet tot het volgen van talloze andere cursussen op
                Pluralsight.</p>
        </>
    )
};

export default Baelor;
