import React from 'react';

const Careathon = () => {
    return (
        <>
            <span className="display-4">Care-athon</span>
            <hr/>
            <p>Nadat wij tien uur op de bus van Berlijn naar huis gezeten hadden, konden we maar even terug op adem
                komen. Enkele dagen later stond het volgende event alweer op de planning: de care-athon. Ik had gekozen
                om de opdracht voor G-karate Vlaanderen uit te werken. Ik heb zelf vijftien jaar lang karate gedaan en
                ik wou mij dan ook inzetten voor deze sport. Ik vond het belangrijk dat de sport, waar ik jarenlang
                plezier van heb gehad, toegankelijk is voor iedereen.</p>
            <p>ICP-Move is de vzw die de opdracht voor deze hackathon samengesteld heeft. Zij willen door middel van
                eenvoudige bewegingen, waarvan de grondbeginselen van karate komen, mensen met speciale behoeften
                aanzetten tot bewegingen. Er wordt gebruik gemaakt van kleuren, iconen en cijfers om deze bewegingen om
                te vormen tot allerlei leuke trainingen. Momenteel geeft een G-coach deze training, maar de bedoeling is
                om deze trainingen toegankelijk te maken voor iedereen, op eender welke plaats.</p>
            <p>De doelstelling van deze opdracht is om een applicatie te ontwikkelen die deze training in digitale vorm
                overbrengt aan de leerling. Op deze manier is het mogelijk om te sporten in scholen, thuis of in
                revalidatiecentra. Zo kan een trainer ook een beter inzicht krijgen over de voortgang die een leerling
                maakt. De trainer zou ook gemakkelijk nieuwe oefeningen moeten kunnen toevoegen. Onze focus lag vooral
                om de oefeningen te visualiseren in de applicatie.</p>
            <div className="illustration">
                <img src="/images/activity/care_1.jpg" alt="care"/>
                <p className="text-muted">Een voorbeeld van een oefening</p>
            </div>
            <p>De eerste voormiddag van deze tweedaagse hackathon, kregen wij een korte inleiding van een trainer en een
                G-sporter. Door deze uitleg kregen we al onmiddellijk een beter inzicht in de doelstellingen van deze
                applicatie. Nadat we onze T-shirts hadden aangedaan, konden we beginnen met een eerste
                brainstormsessie.</p>
            <div className="illustration">
                <img src="/images/activity/G-karate.jpg" alt="care"/>
                <p className="text-muted">Demonstratie op de eerste dag</p>
            </div>
            <p>Wij zijn begonnen met het doornemen van alle aangereikte informatie. We kregen een uitgebreid pdf-bestand
                met allerlei oefeningen en terminologie. De afgesproken kleuren, iconen en cijfers waren belangrijk,
                omdat de leerlingen die al gewoon waren. Het was dan ook zeer voor de hand liggend dat wij deze ook
                gebruiken in onze applicatie. Daarnaast hadden wij het idee om de trainingen ook nog een extra visuele
                voorstelling te geven. Hiervoor hadden wij een animatie in gedachten. De applicatie zou in Flutter
                uitgewerkt worden.</p>
            <p>Ikzelf had nog nooit gewerkt met Flutter. Daarom heb ik mijzelf nuttig gemaakt door enkele
                voorbeeldanimaties van een training uit te werken. Hiervoor heb ik een programma gebruikt dat “Pivot
                Animator” noemt. Je moet wel frame per frame het mannetje opnieuw tekenen, wat tamelijk omslachtig is.
                We hadden maar twee dagen om de applicatie vorm te geven, dus besloten we toch om deze oplossing te
                gebruiken.</p>
            <div className="illustration">
                <img src="/images/activity/stick-figure.gif" alt="care"/>
                <p className="text-muted">Een animatie van een oefening</p>
            </div>
            <p>Tijdens het ontwerpen kregen wij ook feedback van een G-sporter. Hij had veel ervaring met de trainingen
                en had dus een goede visie van het gewenste resultaat. Onder de middag konden we genieten van broodjes
                en pizza. Deze werd aangeboden door de sponsors van deze hackathon: PXL, Cegeka en Xplore Group.</p>
            <p>Na twee dagen ontwerpen en programmeren volgde er een stemming die de twee beste opdrachten bekend
                maakte. Deze twee groepen mochten, samen met de zes beste groepen van de andere onderwerpen, hun idee
                voor iedereen voorstellen. Daarna mochten wij stemmen wie de ultieme winnaar was. Daarmee was deze
                tweedaagse hackathon dan ook afgesloten.</p>
            <p>Voordat de care-athon begonnen was, had ik niet echt veel zin om mee te doen. Ik was juist terug van
                Duitsland, waar ik net een hackathon had meegedaan. Ook vond ik het minder leuk dat wij niet mochten
                kiezen met wie wij een groep vormden. Het was uiteindelijk wel leuk om eens met andere mensen te werken,
                maar ik denk dat ik een beter resultaat had kunnen bekomen als ik zelf een groep had kunnen
                samenstellen.</p>
            <p>Gelukkig had ik na de eerste voormiddag al een stuk meer motivatie om de opdracht uit te werken. Ik kon
                mij echt in de opdracht vinden en had dus ook al een paar ideeën voor de applicatie. Persoonlijk vond ik
                wel dat dit meer een opdracht was voor een langdurig project en minder voor een hackathon. Je kreeg een
                hele hoop gegevens die je moest verwerken in het resultaat. Er was dus weinig ruimte voor nieuwe
                elementen of innovatie. Er werd ook verwacht dat er al effectief iets uitgewerkt moest worden. Als ik
                aan een hackathon denk, denk ik aan prototypes, mockups en het bedenken van nieuwe features. Ik had de
                opdracht dus graag uitgewerkt in enkele weken, maar niet op twee korte dagen.</p>
            <p>De reden waarom ik dit uiteindelijk wel een geslaagde activiteit vond, is dat het mij heeft laten inzien
                dat je op een korte tijd toch iets knap kan maken. In het begin van deze hackathon dacht ik dat wij in
                twee dagen nooit iets toonbaar konden produceren. Toch was ik op het einde best wel fier op het
                resultaat dat deze groep naar voor kon brengen.</p>
        </>
    )
};

export default Careathon;
