import React from 'react';
import {Engagement, Innovation, International, Personal, Seminar, Time} from "../../data/activity_data";
import {Article} from "./Pin";

const Legend = () => {
    const items = [Innovation, International, Seminar, Personal, Engagement, Time, Article];

    const itemPin = item => {
        switch (item) {
            case Innovation:
                return <div className="pin-innovation"><i className="fa fa-flask"/></div>;
            case International:
                return <div className="pin-international"><i className="fa fa-plane"/></div>;
            case Seminar:
                return <div className="pin-seminar"><i className="fa fa-microphone"/></div>;
            case Personal:
                return <div className="pin-personal"><i className="fa fa-user"/></div>;
            case Engagement:
                return <div className="pin-engagement"><i className="fa fa-walking"/></div>;
            case Time:
                return <div className="pin-time"><i className="fa fa-calendar"/></div>;
            case Article:
                return <div className="pin-article"><i className="fa fa-pen"/></div>;
            default:
                return undefined;
        }
    };

    return (
        <div className="legend">
            {items.map((item, i) => {
                return (
                    <div key={i} className="legend-item">
                        {itemPin(item)}
                        <p>{item}</p>
                    </div>
                )
            })}
        </div>
    )
};

export default Legend;
