import React from 'react';
import VPW from "./VPW";
import Baelor from "./Baelor";
import Germany from "./Germany";
import Careathon from "./Careathon";

import {
    VPW as ROUTE_VPW,
    Baelor as ROUTE_BAELOR,
    Germany as ROUTE_GERMANY,
    Careathon as ROUTE_CARE,
    Profile as ROUTE_PROFILE,
    Reflection as ROUTE_REFLECTION
} from "../../data/activity_data";
import Profile from "./Profile";
import Reflection from "./Reflection";
import {Link} from "react-router-dom";

class Article extends React.Component {
    state = {
        id: undefined
    };

    componentDidMount() {
        this.loadArticle();
    }

    componentDidUpdate(prevProps) {
        if (this.props.match.params.id !== prevProps.match.params.id) {
            this.loadArticle();
        }
    }

    loadArticle = () => {
        const {id} = this.props.match.params;
        this.setState({id: Number(id)});
        window.scrollTo(0, 0)
    };

    renderArticle = (id) => {
        if (id === ROUTE_VPW) return <VPW/>;
        else if (id === ROUTE_BAELOR) return <Baelor/>;
        else if (id === ROUTE_GERMANY) return <Germany/>;
        else if (id === ROUTE_CARE) return <Careathon/>;
        else if (id === ROUTE_PROFILE) return <Profile/>;
        else if (id === ROUTE_REFLECTION) return <Reflection/>;
        else return <>Wrong ID</>;
    };

    render() {
        const {id} = this.state;

        return (
            <div className="article-container">
                {this.renderArticle(id)}
                <Link to={"/"} className="btn btn-back">
                    <i className="fa fa-arrow-left"/>
                </Link>
            </div>
        )
    }
}

export default Article;
