import React from 'react';
import "./article.scss";

const VPW = () => {
    return (
        <>
            <span className="display-4">VPW 2018</span>
            <hr/>
            <p>De Vlaamse Programmeerwedstrijd (VPW) van 2018 was één van de eerste uitdagingen die ik aan de PXL ben
                aangegaan. In mijn twee jaar op de universiteit kregen wij wel opgaves van vorige edities om op te
                lossen, maar ik had nog nooit geprogrammeerd in competitieverband. De VPW was dat jaar in Hasselt
                georganiseerd en was dus de ideale gelegenheid om zelf eens mee te doen. Hoewel ik zelf nog maar in het
                eerste jaar zat, had ik er het volste vertrouwen in dat ik goed zou presteren. Dus ik schreef mij samen
                met enkele vrienden uit het tweede jaar in.</p>
            <p>Ik was vastberaden om een goede score te halen op deze wedstrijd en wou mezelf dus extra goed
                voorbereiden. Daarom had ik enkele voorbeeldoefeningen gedownload om deze thuis uit te proberen. Het
                verschil was dat ik nu wel op mijn gemak thuis zat, in plaats van in een volle zaal studenten. Deze
                oefeningen gingen goed, dus ik kon met het volste vertrouwen naar de wedstrijd trekken.</p>
            <p>Die dag zelf had ik ook veel zin om te beginnen programmeren, ook al had ik wat zenuwen. We gingen met
                z’n drieën onze T-shirts halen en installeerden ons op onze plaats. De VPW was in onze eigen school, dus
                waren wij een van de eersten daar. Toen de zaal geleidelijk aan vol begon te lopen, viel het ons echt op
                hoe druk het eigenlijk was. Daarna volgde er nog een korte inleiding, maar na een uurtje wachten, konden
                we eindelijk beginnen.</p>
            <p>De eerste opgave bestond uit een matrix die een wereldkaart voorstelde. Elk van deze cellen had een
                letter. Cellen met dezelfde letter vormden dus samen één land. Onze opdracht was om uit te zoeken
                hoeveel buurlanden een land had. Aan de hand van de matrixvormige landkaart en een gegeven land, moesten
                wij dus één specifiek getal teruggeven.</p>
            <div className="illustration">
                <img src="/images/activity/vpw1.png" alt="vpw"/>
                <p className="text-muted">Eerste opgave VPW 2018</p>
            </div>
            <p>De tweede opgave was getiteld “fracking”. De input van deze vraag was opnieuw een matrix, maar nu konden
                de cellen bestaan uit een puntje of een sterretje. Deze matrix stelde een grondstructuur voor, met harde
                en zachte delen. Elke dag verzakte deze grond. Wij moesten aan de hand van een algoritme bepalen hoelang
                dat zou duren.</p>
            <div className="illustration">
                <img src="/images/activity/vpw2.png" alt="vpw"/>
                <p className="text-muted">Tweede opgave VPW 2018</p>
            </div>
            <p>De makkelijkste opgave was waarschijnlijk de middelste. Voor deze opgave kreeg je een lijst transacties
                van een bank. Deze transacties hadden ook een extra kost. Onze opdracht was om voor de bank de
                transacties te ordenen. Zij hadden graag zo veel mogelijk extra transactiekosten.</p>
            <div className="illustration">
                <img src="/images/activity/vpw3.png" alt="vpw"/>
                <p className="text-muted">Derde opgave VPW 2018</p>
            </div>
            <p>De voorlaatste opdracht bestond uit het ontcijferen van een satellietsignaal. De invoer bestond hier uit
                een lijst van bits. Wij moesten hieruit de boodschap ontcijferen met een zelfgeschreven algoritme.</p>
            <div className="illustration">
                <img src="/images/activity/vpw4.png" alt="vpw"/>
                <p className="text-muted">Vierde opgave VPW 2018</p>
            </div>
            <p>De laatste opdracht diende om fouten in slingers op te sporen. Wij kregen een slinger die voorgesteld was
                door puntjes en sterretjes. Wij moesten daarna bepalen of de slinger correct opgesteld was. Als deze
                fout was, moesten wij deze slinger corrigeren.</p>
            <div className="illustration">
                <img src="/images/activity/vpw5.png" alt="vpw"/>
                <p className="text-muted">Vijfde opgave VPW 2018</p>
            </div>
            <p>Nadat we de opdracht uitgewerkt hadden, duurde het even voor de uitslag bekend gemaakt kon worden. Om de
                tijd te vullen, had de PXL broodjes voorzien. Daarna was het tijd voor de prijsuitreiking. Hoewel we
                juist buiten de top 10 geëindigd waren, ging ik toch voldaan naar huis.</p>
            <p>De Vlaamse Programmeerwedstijd was voor mij zeker een positieve ervaring, maar het was toch niet wat ik
                ervan verwacht had. Thuis lukte het mij om alle oefeningen op te lossen, maar op de wedstrijd zelf lukte
                dat zeker niet. Het was mij vooral moeilijk om te concentreren in een volle zaal. Niet alleen het lawaai
                stoorde mij, maar na een tijdje was het ook ontzettend warm. Ik heb ook geleerd dat het moeilijk is om
                drie uur onafgebroken na te denken. Het had zeker geholpen om even een korte pauze te nemen.</p>
            <div className="illustration">
                <img src="/images/activity/vpw.jpg" alt="pxl"/>
                <p className="text-muted">Sfeerbeeld VPW 2018</p>
            </div>
            <p>Ik heb ook geleerd dat ik minder goed programmeer wanneer er iemand meekijkt op mijn computerscherm. Voor
                deze wedstrijd mocht er maar één laptop per drie gebruikt worden. Ik vond het lastig om te programmeren
                terwijl mijn teamleden ook probeerden mee te helpen. Dit is iets dat ik in latere projecten ook
                ondervonden heb.</p>
            <p>Maar er is nog één ding dat mij zeker is bijgebleven. Wij hadden drie uur de tijd om vijf opgaves uit te
                werken. Op zo’n korte tijd denk je dus vooral aan de resultaten en niet aan de kwaliteit van de code die
                je schrijft. Dit heeft mij doen inzien dat je bij andere projecten echt wel je tijd moet nemen om
                kwaliteitsvolle code te schrijven.</p>
            <p>Om kort samen te vatten was dit een leuke uitdaging. Deze editie heeft mij ook aangezet om in 2019 terug
                mee te doen aan de VPW in Antwerpen. De kennis die ik de eerst keer heb opgedaan, heb ik dat jaar
                gebruikt om een plaats in de top 10 te veroveren.</p>
        </>
    )
};

export default VPW;
