import React from 'react';
import {Link} from "react-router-dom";
import Pin, {Article} from "./Pin";

const Activity = ({articleId, title, type, date, duration, location, img, text}) => {

    const generateImage = img => img ? <img src={img} alt="cover" className="card-img"/> : undefined;

    const checkClickable = articleId => articleId ? " clickable" : " non-clickable";

    const generateDuration = duration => duration ?
        <p className="text-muted"><i className="far fa-clock"/> {duration}</p> : undefined;

    const generateArticlePin = articleId => articleId ? <Pin type={Article}/> : undefined;

    return (
        <div className="timeline-item">
            <Link className={"card timeline-panel" + checkClickable(articleId)}
                  to={articleId ? "/article/" + articleId : "#"}>
                {generateImage(img)}
                <div className="card-body">
                    <div className="panel-header">
                        <h5>{title}</h5>
                        <div className="badge badge-location">@ {location}</div>
                    </div>
                    <p>{text}</p>
                </div>
                <div className="card-footer">
                    <p className="text-muted"><i className="far fa-calendar-alt"/> {date}</p>
                    {generateDuration(duration)}
                </div>
            </Link>
            <Pin type={type}/>
            {generateArticlePin(articleId)}
        </div>
    )
};

export default Activity;
