import React from 'react';

const Germany = () => {
    return (
        <>
            <span className="display-4">Duitslandreis</span>
            <hr/>
            <p>Hoewel ik er eerst tegenop keek, zat ik op 12 februari 2019 toch te wachten tot de bus eindelijk vertrok.
                Eerst had ik niet zo veel zin in een saaie schoolreis naar Duitsland, maar na de verhalen van vorige
                jaren gehoord te hebben, kreeg ik meer en meer zin om op die bus te stappen. Dus na een job event in de
                namiddag, vertrokken we eindelijk richting Paderborn.</p>
            <p>Na een vier uur durende rit, was iedereen tamelijk geradbraakt. Toch gingen de meeste van ons al op pad
                om de stad wat te verkennen. Op het eerste gezicht leek het maar een dooie boel. Het was ook maar een
                doodnormale woensdagavond. Toch hadden wij een paar leuke caféetjes gevonden. We hadden het ook niet te
                laat gemaakt, want de dag erna moesten we al vroeg vertrekken naar Fraunhofer.</p>
            <p>Het eerste deel van de Duitslandreis bestond uit een hackathon van de Fraunhofer Society, een Duitse
                onderzoeksorganisatie. Voor deze hackathon zocht het bedrijf Heroal een manier om hun automatische
                “window shutters” te revolutioneren.</p>
            <p>Toen we aankwamen in het hypermoderne gebouw van Fraunhofer, kregen we een korte inleiding over de
                hackathon. Daarna werden we in groepen van vijf personen. Na een groepskennismaking, mochten we er dan
                ook eindelijk invliegen.</p>
            <div className="illustration">
                <img src="/images/activity/berlin.jpg" alt="germany"/>
                <p className="text-muted">De groep werkt een idee uit</p>
            </div>
            <p>De opdracht verliep in verschillende fasen. Tijdens de voormiddag van de eerste dag moet er al een eerste
                idee op tafel liggen. In de namiddag moest dat idee namelijk al voorgesteld worden. Wij hadden het idee
                om alle informatie van de motor van een rolluik te verzamelen. Op basis daarvan zouden wij dan
                voorspellingen doen. Deze informatie moest ook mooi in een dashboard worden weergegeven.</p>
            <div className="illustration">
                <img src="/images/activity/berlin_1.jpg" alt="germany"/>
                <p className="text-muted">Voorbeeld van het dashboard</p>
            </div>
            <p>Na de eerste lading van pitches, hadden we nog de rest van de dag om de feedback die we kregen te
                verwerken. We moesten toen ook al beginnen nadenken over de mediacampagne die wij wouden voeren met ons
                product. We hadden de opdracht gekregen om enkele facebook advertenties te ontwerpen. Daarnaast moesten
                we ook een landingspagina maken om ons prototype voor te stellen. Ten slotte moest er op deze website
                een zelfverzonnen slogan komen.</p>
            <div className="illustration">
                <img src="/images/activity/ad.png" alt="germany"/>
                <p className="text-muted">Een advertentie van ons product</p>
            </div>
            <p>Na een welverdiende nachtrust, begon de tweede dag weer met een pitch. Hierna kregen we feedback op de
                content die wij de dag daarvoor ontworpen hadden. De rest van de dag mochten wij benutten om de laatste
                hand te leggen aan ons ontwerp.</p>
            <p>Rond 15u volgde er een laatste pitch voor een jury, bestaande uit werknemers van Heroal, PXL en
                Fraunhofer. Hierbij gaven elk van deze vijf juryleden voor de laatste keer commentaar op ons afgeleverde
                werk. Toen iedereen aan de beurt was geweest, kozen zij een winnaar.</p>
            <p>Na twee dagen inspanning, kon gelukkig nu de ontspanning beginnen. Ook al moesten we eerst nog enkele
                uren op de bus zitten, iedereen stond te popelen om een stapje in het nachtleven van Berlijn te zetten.
                Toen we eindelijk aankwamen, herenigden wij ons moet onze collega’s die de andere reis naar Berlijn
                gekozen hadden. Samen trokken we met de metro naar Berlijn en bleven we daar tot in de vroege
                uurtjes.</p>
            <div className="illustration">
                <img src="/images/activity/berlin_2.jpg" alt="germany"/>
                <p className="text-muted">Eén van onze hotelkamers</p>
            </div>
            <p>Na drie dagen toeristische uitstapjes en lange feestnachten, was het spijtig genoeg tijd om terug naar
                huis te gaan. Maandagochtend vertrokken wij terug naar België, om na negen lange uren eindelijk terug in
                ons eigen bedje te slapen.</p>
            <p>Hoewel het grootste deel van de Duitslandreis zich in Berlijn afspeelde, heb ik toch het meeste geleerd
                in Paderborn. Het was inderdaad wel leuk om in Berlijn wat stoom af te laden met mijn vrienden. Iedereen
                had een lang semester achter de rug. De lessen, maar voor het IT-project, zorgden ervoor dat iedereen
                wat ontspanning nodig had. Maar uiteindelijk was dit maar gewoon een toeristische reis zoals je er al
                meer gedaan hebt.</p>
            <p>In Paderborn daarentegen, had je echt de kans om jezelf te ontwikkelen als developer. Hackathons zijn
                voor ons nu niet echt zo speciaal, maar de kennis die zich in Fraunhofer bevond, hadden wij nog nooit
                ergens anders gezien. De feedback die wij te horen kregen, was enorm doordacht. Als je met deze mensen
                sprak, had je echt het idee dat zij wisten waarover ze het hadden.</p>
            <p>Verder heb ik ook ondervonden hoe moeilijk het is om een goede pitch te geven. Er waren maar een paar
                groepen waarvan ik dacht: “Dit product wil ik zelf kopen!”. Ook vond ik het moeilijk om zelf een goede
                verkopersspeech te verzinnen. Niet alleen de inhoud was van belang, de pitch mocht namelijk maar drie
                minuten duren. Indien je pitch langer duurde, werd je gewoon gestopt. Het was dus zeer belangrijk om
                deze goed voor te bereiden.</p>
            <div className="illustration">
                <img src="/images/activity/pitch.jpg" alt="germany"/>
                <p className="text-muted">De eindpitch van ons idee</p>
            </div>
            <p>Ten slotte zijn hackathons ook altijd een goede teambuilding. Na twee dagen intensief samengewerkt te
                hebben, ken je je collega’s ook een stukje beter. Je leert ook heel wat bij over werken in groep.</p>
        </>
    )
};

export default Germany;
