class ActivityData {
    constructor(articleId, title, type, date, duration, location, img, text) {
        this.articleId = articleId;
        this.title = title;
        this.type = type;
        this.date = date;
        this.duration = duration;
        this.location = location;
        this.img = img;
        this.text = text;
    }
}

